<template>
	<view>
		<uni-popup ref="popup">
			<view class="chart-view">
				<view class="column-view">
					<view class="charts-box">
						<qiun-data-charts type="column" :opts="opts" :chartData="firstChartData" />
						<view style="width: 100%; text-align: center; margin-top: 30px" v-if="campData.length > 0">
							{{ campData[0].campName }}得分情况
						</view>
					</view>
					<view class="charts-box">
						<qiun-data-charts type="column" :opts="opts" :chartData="secondChartData" />
						<view style="width: 100%; text-align: center; margin-top: 30px" v-if="campData.length > 0">
							{{ campData[1].campName }}得分情况
						</view>
					</view>
				</view>

				<view class="charts-box-pie" style="margin-top: 20px">
					<qiun-data-charts type="pie" :opts="pieOpts" :chartData="pieChartData" />
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
export default {
	name: 'final-summary',
	data() {
		return {
			chartData: {},
			//您可以通过修改 config-ucharts.js 文件中下标为 ['column'] 的节点来配置全局默认参数，如都是默认参数，此处可以不传 opts 。实际应用过程中 opts 只需传入与全局默认参数中不一致的【某一个属性】即可实现同类型的图表显示不同的样式，达到页面简洁的需求。
			//您可以通过修改 config-ucharts.js 文件中下标为 ['column'] 的节点来配置全局默认参数，如都是默认参数，此处可以不传 opts 。实际应用过程中 opts 只需传入与全局默认参数中不一致的【某一个属性】即可实现同类型的图表显示不同的样式，达到页面简洁的需求。
			opts: {
				color: [
					'#1890FF',
					'#91CB74',
					'#FAC858',
					'#EE6666',
					'#73C0DE',
					'#3CA272',
					'#FC8452',
					'#9A60B4',
					'#ea7ccc'
				],
				padding: [15, 15, 0, 5],
				enableScroll: true,
				legend: {},
				xAxis: {
					disableGrid: true,
					scrollShow: true,
					itemCount: 4
				},
				yAxis: {
					data: [
						{
							min: 0
						}
					]
				},
				extra: {
					column: {
						type: 'group',
						width: 30,
						activeBgColor: '#000000',
						activeBgOpacity: 0.08
					}
				}
			},
			pieChartData: {},
			//您可以通过修改 config-ucharts.js 文件中下标为 ['pie'] 的节点来配置全局默认参数，如都是默认参数，此处可以不传 opts 。实际应用过程中 opts 只需传入与全局默认参数中不一致的【某一个属性】即可实现同类型的图表显示不同的样式，达到页面简洁的需求。
			pieOpts: {
				color: ['#1890FF', '#EE6666'],
				padding: [5, 5, 5, 5],
				enableScroll: false,
				extra: {
					pie: {
						activeOpacity: 0.5,
						activeRadius: 10,
						offsetAngle: 0,
						labelWidth: 15,
						border: false,
						borderWidth: 3,
						borderColor: '#FFFFFF'
					}
				}
			},
			categories: [],
			firstSeries: [
				{
					name: '指挥口令规范性',
					data: []
				},
				{
					name: '指挥要素完备性',
					data: []
				},
				{
					name: '打击效果',
					data: []
				}
			],
			secondSeries: [
				{
					name: '指挥口令规范性',
					data: []
				},
				{
					name: '指挥要素完备性',
					data: []
				},
				{
					name: '打击效果',
					data: []
				}
			],
			firstChartData: {},
			secondChartData: {},
			pieData: []
		};
	},
	props: {
		dataArray: {
			type: Array,
			default() {
				return [];
			}
		},
		round: {
			type: Number,
			default: 0
		},
		campData: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	watch: {
		dataArray: {
			handler(newVal) {
				this.formatterData();
				this.pieData = [];
				for (var i in newVal) {
					if (newVal[i].campId == this.campData[0].campId) {
						console.log('newVal[i].score != -1: ', newVal[i].score == -1);
						if (newVal[i].score == -1) {
							if (newVal[i].type == 1) {
								this.firstSeries[0].data = newVal[i].scores.split(',');
							}
							if (newVal[i].type == 2) {
								this.firstSeries[1].data = newVal[i].scores.split(',');
							}
							if (newVal[i].type == 3) {
								this.firstSeries[2].data = newVal[i].scores.split(',');
							}
						} else {
							this.pieData.push({ name: this.campData[0].campName, value: newVal[i].score });
						}
					} else {
						if (newVal[i].score == -1) {
							if (newVal[i].type == 1) {
								this.secondSeries[0].data = newVal[i].scores.split(',');
							}
							if (newVal[i].type == 2) {
								this.secondSeries[1].data = newVal[i].scores.split(',');
							}
							if (newVal[i].type == 3) {
								this.secondSeries[2].data = newVal[i].scores.split(',');
							}
						} else {
							this.pieData.push({ name: this.campData[1].campName, value: newVal[i].score });
						}
					}
				}
				this.getServerData();
				this.getPieServerData();
			}
		},
		round: {
			handler(newVal) {
				for (let i = 0; i <= newVal - 1; i++) {
					this.categories.push('回合' + i);
				}
				console.log('this.categories: ', this.categories);
				this.getServerData();
				// this.getPieServerData();
			}
		}
	},
	mounted() {},
	methods: {
		open() {
			this.$refs.popup.open();
		},
		formatterData() {},
		getServerData() {
			console.log('数据加载');
			//模拟从服务器获取数据时的延时
			// setTimeout(() => {
			//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
			let res = {
				categories: this.categories,
				series: [
					{
						name: '指挥口令规范性',
						data: [35, 36, 31, 33, 13, 34]
					},
					{
						name: '指挥要素完备性',
						data: [18, 27, 21, 24, 6, 28]
					},
					{
						name: '打击效果',
						data: [18, 27, 21, 24, 6, 28]
					}
				]
			};
			this.firstChartData = JSON.parse(JSON.stringify({ categories: this.categories, series: this.firstSeries }));
			this.secondChartData = JSON.parse(
				JSON.stringify({ categories: this.categories, series: this.secondSeries })
			);
			console.log('this.firstChartData: ', this.firstChartData);
			// }, 500);
		},
		getPieServerData() {
			//模拟从服务器获取数据时的延时
			// setTimeout(() => {
			//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
			let res = {
				series: [
					{
						data: this.pieData
					}
				]
			};
			this.pieChartData = JSON.parse(JSON.stringify(res));
			console.log('pieChartData: ', res);
			// }, 500);
		}
	}
};
</script>

